import React from 'react';
import './AboutMe.css';
import photoStudent from '../../images/student-img.png';

function AboutMe() {
  return(
    <section className="student">
      <h2 className="student__heading"></h2>
      <div className="student__info">
        <img className="student__image" alt=" " src={photoStudent} />
        <p className="student__name"></p>
        <p className="student__occupation">-, 30 </p>
        <p className="student__about">     ,    .     
           .    ,    .   .  2015      .  ,     -,   -     .</p>
        <a className="student__portfolio-link" href="https://github.com/lisaquien" target="_blank" rel="noreferrer">Github</a>
      </div>
    </section>
  );
}

export default AboutMe;