import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Profile.css';
import FormInput from '../FormInput/FormInput';
import FormButton from '../FormButton/FormButton';

function Profile(props) {
  const { user } = props;

  const [inputsEnabled, setInputsEnabled] = useState(false);

  // -   , , 
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');

  // -   , , 
  function handleNameInput(e) {
    setName(e.target.value);
  }
  function handleEmailInput(e) {
    setEmail(e.target.value);
  }

  function toggleInputs() {
    setInputsEnabled(!inputsEnabled);
  }

  return(
    <div className="profile">
      <div className="profile__container">
        <p className="profile__greeting">, {user.name}!</p>
        <div className="profile__info">
          <form className="form form_profile">
            <div className="profile__name">
              <FormInput
                componentName="profile"
                labelText=""
                type="text"
                name="name"
                id="profile-name"
                placeholder={user.name}
                onChange={handleNameInput}
                value={name || ""}
                minLength="2"
                maxLength="30"
                disabled={!inputsEnabled && "disabled"}
              />       
            </div>
            <div className="profile__email">
              <FormInput
                componentName="profile"
                labelText="E-mail"
                type="email"
                name="email"
                id="profile-email"
                placeholder={user.email}
                onChange={handleEmailInput}
                value={email || ""}
                disabled={!inputsEnabled && "disabled"}
              /> 
            </div>
            <div className="profile__ctrl-panel">
              {inputsEnabled ? <FormButton componentName="profile" buttonText="" onClick={toggleInputs} /> :
                <>
                  <Link to="" className="profile__link" onClick={toggleInputs}></Link>
                  <Link to="" className="profile__link">  </Link>
                </>
              }
            </div>
          </form>  
        </div>
      </div>
    </div>
  );
}

export default Profile;